import React, { Component } from 'react'
import * as emailjs from 'emailjs-com';
import {  Form } from 'reactstrap';
import {Textfield,Button} from 'react-mdl';
// import { Form, Input, Button, Checkbox } from 'antd';
// import isEmail from 'validator/lib/isEmail';



const Swal = require('sweetalert2');


class Contact extends Component {


  onChange(value) {
    console.log("Captcha value:", value);
  }
  state = {
    name: '',
    email: '',
    subject: '',
    message: '',
  }
  
   sendEmail(e) {
     if(this.state.name === '' || this.state.name === null ||
        this.state.email === '' || this.state.email === null ||
        this.state.subject === '' || this.state.subject === null ||
        this.state.message === '' || this.state.message === null 
     ){
       Swal.fire({
        position: 'top',
        icon: 'error',
        title: 'Pleased filled the form properly',
        showConfirmButton: false,
        timer: 1500
      })
      //  this.state.name.focus()
       e.preventDefault();
     } else {
    e.preventDefault();
    console.log(e);
    var templateParams = {
      from_email: this.state.email,
      from_name:this.state.name,
      message: this.state.message,
      subject : this.state.subject
  };
   
  emailjs.send('nascentsurya_gmail_com', 'template_7benqrvG', templateParams,'user_lskYYvUtXzWX6RDvKFijo')
      .then(function(response) {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Your Information has been send',
          showConfirmButton: false,
          timer: 1500
        })
         console.log('SUCCESS!', response.status, response.text);
      }, function(error) {
         console.log('FAILED...', error);
      });
      this.resetForm();
    }
  }

resetForm() {
    this.setState({
      name: '',
      email: '',
      subject: '',
      message: '',
    })
  }

handleChange = (param, e) => {
    this.setState({ [param]: e.target.value })
  }


Validation(){ 

}
render() {
    return (
      <div style={{margin:"8%"}} className="contact-background">
        <div className="card">
          <div className="card-header">
              GET IN TOUCH
          </div>
          <div className="card-body">
  
          <Form>
          <Textfield
               onChange={this.handleChange.bind(this, 'email')}
               pattern="[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?"
               error="Format of the email must be abcd@gmail.com"
                label="Email..."
                floatingLabel
                value={this.state.email}
                style={{width: '400px'}}

            />
          <br/>
          
              <Textfield
                  label="NAME..."
                  floatingLabel
                  value={this.state.name}
                  onChange={this.handleChange.bind(this, 'name')}
                  style={{width: '400px'}}

                  />
                <br/>
              <Textfield
                    label="Subject"
                    floatingLabel
                    value={this.state.subject}
                    onChange={this.handleChange.bind(this, 'subject')}
                    style={{width: '400px'}}

                />
              <br/>
              <Textfield
                    label="Message"
                    floatingLabel
                    value={this.state.message}
                    onChange={this.handleChange.bind(this, 'message')}
                    style={{width: '400px'}}
                    rows={3}
                />
            <br/>
            <Button raised colored onClick={this.sendEmail.bind(this)}>Button</Button>
          </Form>
         
  {/* document.body */}
  </div>
</div>
  </div>
    )
  }
}
export default Contact